<template>
  <v-container fluid pt-0>
    <v-layout justify-center>
      <v-flex xs12 sm12 md9 lg6>
        <v-layout column nowrap>
          <v-flex mb-3 title font-weight-regular primary--text>
            Notifications
          </v-flex>
          <v-layout column>
            <v-layout
              v-for="(notification, i) in notifications"
              :key="i"
              card
              pa-2
              mb-3
              align-center
            >
              <v-flex xs2>
                <v-btn
                  depressed
                  fab
                  dark
                  :large="!$vuetify.breakpoint.xsOnly"
                  :small="$vuetify.breakpoint.xsOnly"
                  :color="getColor(notification.level)"
                >
                  <v-icon>{{ getIcon(notification.level) }}</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs10 pa-2>
                <v-layout column>
                  <v-flex font-weight secondary--text pb-1>
                    {{ notification.text }}
                  </v-flex>
                  <v-flex font-weight-light>
                    {{ notification.createTime }}
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-layout>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["notifications"])
  },
  methods: {
    getColor(level) {
      let color = "";
      switch (level) {
        case "success":
          color = "success";
          break;
        case "warning":
          color = "info";
          break;
        case "error":
          color = "error";
          break;
        default:
          color = "info";
          break;
      }
      return color;
    },
    getIcon(level) {
      let icon = "";
      switch (level) {
        case "success":
          icon = "check_circle";
          break;
        case "warning":
          icon = "info";
          break;
        case "error":
          icon = "error_outline";
          break;
        default:
          icon = "info";
          break;
      }
      return icon;
    }
  }
};
</script>

<style></style>
